<template>
  <div class="w3-margin-left w3-margin-bottom">

    <div v-if="modified_cats.length > 0" class="w3-bar w3-hide-small">
      <router-link :to="{name: 'home'}" class="w3-large w3-bar-item w3-button w3-border-pink w3-bottombar"
                   :class="{'w3-pink': category=='all', 'w3-light-gray': category!='all'}">All</router-link>
      <router-link v-for="(c, i) in modified_cats" :key="i"
                   :to="{name: 'category', params: {category: c.name}}"
                   :class="{'w3-pink': c.is_current, 'w3-light-gray': !c.is_current}"
                   class="w3-large w3-bar-item w3-button w3-border-pink w3-bottombar">
                   {{ c.name }}</router-link>
    </div>
    
    <div v-else class="w3-bar w3-hide-small">
      <h1 class="fa fa-spinner w3-spin"></h1>
    </div>
    
    <div v-if="modified_cats.length > 0" class="w3-large w3-dropdown-hover w3-hide-medium w3-hide-large">
      <button class="w3-button w3-pink"><i class="fa fa-caret-down"></i>
        <span v-if="!this.$route.params.category"> Filters</span>
        <span v-else> {{ this.$route.params.category }}</span>
      </button>
      <div class="w3-dropdown-content w3-bar-block w3-border w3-card-4">
        <router-link v-for="(c, i) in modified_cats" :key="i" v-show="!c.is_current"
                     :to="{name: 'category', params: {category: c.name}}"
                     class="w3-bar-item w3-button w3-light-gray">{{ c.name }}s</router-link>
      </div>
    </div>
    
    <div v-else class="w3-hide-medium w3-hide-large w3-container">
      <h1 class="fa fa-spinner w3-spin"></h1>
    </div>

  </div>
</template>

<script>
export default {
  name: "Categories",
  data() {
    return {
      categories: this.$store.state.categories
    };
  },
  computed: {
    category() {
      var cat;
      if (this.$route.params.category) {
        cat = this.$route.params.category;
      } else if (this.$route.name == "home") {
        cat = "all";
      } else {
        cat = undefined;
      }
      return cat;
    },
    modified_cats() {
      var cats = [];
      var current_cat = this.category;
      for (var i in this.categories) {
        var cat = this.categories[i];
        var c = { name: cat };
        if (cat == current_cat) c.is_current = true;
        else c.is_current = false;
        cats.push(c);
      }
      return cats;
    },
    title() {
      var t = "DSadian";
      if (this.category != "all") t = (this.category + "s").toUpperCase();
      return t;
    }
  },
  metaInfo() {
    return {
      title: this.title
    };
  }
};
</script>

