<template>
  <div>
    <form class="w3-container w3-hide-small w3-hide-medium w3-display-topright w3-margin-top"
          @input.prevent="goSearch" @submit.prevent="goSearch">
      <input v-model="q" type="text"
             class="w3-input w3-hover-light-gray w3-light-white w3-col s10"
             placeholder="Search project..." required/>
      <button type="submit" class="w3-button w3-pink w3-hover-green w3-col s2">
        <i class="fa fa-search"></i>
      </button>
    </form>
  </div>
</template>

<script>
export default {
  name: "ProjectSearchBox",
  data() {
    return { q: "" };
  },
  methods: {
    goSearch() {
      this.$router.push({ name: "home", query: { q: this.q, page: 0 } });
    }
  }
};
</script>

