import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

const store = new Vuex.Store({
  state() {
    var date = new Date();
    var dayNames = [
      "Sunday",
      "Monday",
      "Tuesday",
      "Wednesday",
      "Thursday",
      "Friday",
      "Saturday"
    ];
    var state = {
      projects: [],
      categories: [],
      about: [],
      year: date.getFullYear(),
      day: dayNames[date.getDay()],
      quote: "",
      person: ""
    };
    axios.get("https://dsadian.herokuapp.com/api/days/").then(response => {
      var d = response.data[date.getDay()];
      state.quote = d.quote;
      state.person = d.whose;
    });
    axios.get("https://dsadian.herokuapp.com/api/projects/").then(response => {
      state.projects = response.data;
      for (var p in state.projects) {
        if (!state.categories.includes(state.projects[p].category)) {
          state.categories.push(state.projects[p].category);
        }
      }
    });
    axios.get("https://dsadian.herokuapp.com/api/about/").then(response => {
      state.about = response.data;
    });
    return state;
  }
});

export default store;
