<template>
  <div class="w3-container">
    
    <transition-group name="contact-modal"
                      enter-active-class="animated zoomInUp"
                      leave-active-class="animated zoomOutUp">
      
      <div class="w3-modal" v-show="showModal" key="2">

	      <div class="w3-modal-content animated flip">
          
          <div class="w3-container w3-padding w3-text-white">
			      <span class="closebtn w3-hover-text-red" @click="$emit('hide-contact-modal')"><i class="fa fa-close"></i></span>
			      <h2><i class="fa fa-pencil"></i> Contact me!</h2>
		      </div>

          <div class="w3-container">

            <form class="w3-container w3-padding w3-margin-bottom w3-white w3-center"
                  @submit.prevent="contact">
              
              <input v-model="name" class="w3-input w3-margin-bottom w3-margin-top w3-border-gray w3-hover-border-green" placeholder="Full Name" id="name" required="">
              <input v-model="email" class="w3-input w3-margin-bottom w3-border-gray w3-hover-border-green" placeholder="Email" id="email" required="">
              <textarea v-model="content" class="w3-border-gray w3-hover-border-green" id="message" placeholder="Message..." required=""></textarea>
              
              <div class="w3-right w3-margin-top w3-margin-bottom">
                <button type="reset" class="w3-button w3-padding w3-card-4 w3-red">
                  <b><i class="fa fa-trash"></i> Clear</b>
                </button>
                <button type="submit" class="w3-button w3-padding w3-card-4 w3-green w3-margin-left">
                  <b><i class="fa fa-send"></i> Submit</b>
                </button>
              </div>

            </form>

          </div>

	      </div>
      </div>

    </transition-group>

  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "ContactModal",
  props: {
    showModal: Boolean
  },
  data() {
    return {
      name: "",
      email: "",
      content: ""
    };
  },
  methods: {
    contact() {
      this.$emit("hide-contact-modal");
      axios
        .post("http://dsadian.herokuapp.com/api/contact/", {
          full_name: this.name,
          email: this.email,
          content: this.content
        })
        .then(function() {
          alert("Thank you very much for leaving me a message!");
        })
        .catch(function() {
          alert("Sorry, I did something bad. Try again in a little bit!");
        });
      this.name = "";
      this.email = "";
      this.content = "";
    }
  }
};
</script>

